.header {
  background-size: cover;
  position: relative; }
  .header__inner {
    min-height: 500px;
    margin: 0 auto;
    display: flex;
    align-items: center;
    width: 300px; }
    @media screen and (min-width: 748px) {
      .header__inner {
        width: 748px; } }
    @media screen and (min-width: 1170px) {
      .header__inner {
        width: 1170px; } }

.main-nav {
  position: absolute;
  top: 0;
  left: 0;
  transform: translate(-100%, 0);
  height: 100%;
  background-color: #1c1531;
  display: flex;
  align-items: center;
  transition: all 0.325s 0s ease; }
  .main-nav--active {
    color: #fff; }
  @media screen and (min-width: 748px) {
    .main-nav {
      display: block;
      height: auto;
      transform: translate(0, 0); } }
  .main-nav--open {
    transform: translate(0, 0); }
  .main-nav__list {
    padding: 0 30px;
    margin: 0;
    list-style-type: none; }

.main-nav__link {
  color: #fff;
  text-decoration: none;
  font-family: "Jura", sans-serif;
  font-size: 14px;
  line-height: 36px; }
  .main-nav__link--active {
    color: #fff; }

.main-title__head {
