  align-items: center;
  justify-content: center;
  white-space: nowrap;
  z-index: 10; }
  .side-bar__vk {
    background-image: url(../img/sprite-041120649281925803.png);
    background-position: 0px -44px;
    width: 25px;
    height: 15px;
    position: absolute;
    top: 10px; }
  .side-bar__link {
    transform: rotate(-90deg);
