  .nav-burger--close {
    background-image: url(../img/sprite-041120649281925803.png);
    background-position: -44px 0px;
    width: 40px;
    height: 40px; }
  .nav-burger--hamburger {
    background-image: url(../img/sprite-041120649281925803.png);
    background-position: 0px 0px;
    width: 40px;
    height: 40px; }

